
@font-face {
  font-family: Russia;
  src: url(static/fonts/russia.b2d27c8.ttf);
}
body {
  margin: 0;
  background: #0f4583;
}
#app {
  font-family: Russia, 'Avenir', Helvetica, Arial, sans-serif, '\5FAE\8F6F\96C5\9ED1';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
@-webkit-keyframes rotate {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotate {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}

.c-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  background: #0f4583;
  color: #fff;
  text-align: center;
}
.c-wrapper {
  position: absolute;
  top: 46%;
  left: 0;
  width: 100%;
}
.c-progress {
  position: relative;
  display: inline-block;
  width: 60%;
  height: 4px;
  background: rgba(255,255,255,.2);
  border-radius: 2px;
}
.c-progress__inner {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  background: #fff;
  border-radius: 2px;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
  will-change: width;
}

.c-fixed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* 控制子组件的层叠顺序 */
.z0 {
  z-index: 0;
}
.z1 {
  z-index: 1;
}
.z2 {
  z-index: 2;
}
.z3 {
  z-index: 3;
}
.z4 {
  z-index: 4;
}

/* 底部中央的滚动指示器 */
.c-scrollIndicator {
  position: fixed;
  bottom: 10px;
  left: 0;
  width: 100%;
  text-align: center;
  opacity: .8;
  z-index: 10;
  pointer-events: none;
}
.c-scrollIndicator img {
  width: 32px;
}

